.statistic-card {
  padding: 20px 24px;
  border: 1px solid #e8e8e8;
  border-radius: 2px;
}

.statistic-card__avatar-wrapper {
  float: left;
  margin-top: 3px;
  margin-right: 20px;
}

.statistic-card__dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  margin-right: 10px;
  border-radius: 50%;
}

.statistic-card__topTag {
  width: 100%;
  height: 4px;
}

.statistic-card__title {
  margin-bottom: 4px;
  color: rgba(0, 0, 0, 0.45);
  font-size: 14px;
}

.statistic-card__number {
  margin-right: 30px;
  color: rgba(0, 0, 0, 0.85);
  font-size: 30px;
}

.statistic-card__percentage {
  color: rgba(0, 0, 0, 0.45);
  font-size: 16px;
}

.statistic-card__title-wrapper__title {
  padding: 4px 6px;
  color: #fff;
  border-radius: 4px;
}
